// @flow
// Flow Types
import type { LoginData } from '../../types';

export type LoginErrors = {
  identifier?: string,
  password?: string,
  license?: string,
};

// Validates login input
export default function validateInput(data: LoginData): { isValid: boolean, errors: LoginErrors } {
  let errors = {};

  if (!data.identifier) {
    errors.identifier = 'This field is required';
  }

  if (!data.password) {
    errors.password = 'This field is required';
  }

  if (!data.license) {
    errors.license = 'You must accept the license agreement';
  }

  return {
    errors,
    isValid: Object.keys(errors).length === 0,
  };
}
